.imgDiv {
    width: 278px;
    height: 278px;
    border-radius: 8px;
    position: relative;
    margin: 24px;
}

.img {
    width: 100%;
    height: 100%;
    border-radius: 8px;
}

.hover {
    background-color: var(--Cyan);
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 8px;
    opacity: 0;
}

.hover:hover {
    opacity: .7;
}

@media screen and (min-width: 700px) {
    .imgDiv {
        width: 302px;
        height: 302px;
    }
}